<template>
  <div>
  <el-dialog ref='term-detail' title="标注数据" :visible.sync='visible' :size="size" @close='close'>
    <el-tabs type="card" @tab-click="tabClick" value="addAnno" >
      <el-tab-pane label="添加标签" name="addAnno">
        <el-form :model="annotation">
          <el-form-item label="词条内容" size="mini" style="text-align:center">
            <span style="font-size:18px">{{annotation.term}}</span>
          </el-form-item>
          <el-form-item label="词条分类" style="text-align:center">
            <el-select v-model="annotation.type" placeholder="请选择" size="large" filterable>
              <el-option
                v-for="item in annotation.types"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="float: left">{{item.label}}</span>
                <span style="float:right;color:#8492a6; font-size: 13px">{{item.value}}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="同时加入原子词库" style="text-align:center">
            <el-switch
              v-model="annotation.isNewTerm"
              on-color="#13ce66"
              off-color="#ff4949"
              >
            </el-switch>
          </el-form-item>
        </el-form>
        <el-row>
          <el-col :span="24" style="text-align:center">
            <el-button type="primary" @click="mySubmit">标注词条</el-button>
            <el-button @click='visible=false'>关闭</el-button>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="合并原子术语" name='combineAnno'  >
        <el-row>
          <el-col :span="24">
            <el-button-group>
              <el-button type="primary" @click="batchUpdate">批量合并所选项</el-button>
              <el-button type="info" @click="refresh">刷新</el-button>
            </el-button-group>
         <!--  </el-col>
          <el-col :span="12"> -->
            <span style="font-size:15px">合并的原子词为：{{showName}}</span>
          </el-col>
        </el-row>
          <div id="mark-result">
          <el-table
            element-loading-text="加载数据"
            @selection-change="HandleSelectionChange"
            v-loading="loading"
            :data="annotationArr"
            style="width:100%"  
            empty-text="暂无数据"
            height="550"
            highlight-current-row>
            <el-table-column
              type="selection"
              width="55">
            </el-table-column>
            <el-table-column  min-width="100" label="术语ID" header-align="center">
              <template slot-scope="brat">
                  <span>{{brat.row.id}}</span>
              </template>
            </el-table-column>
            <el-table-column min-width="300" label="最终标注" header-align="center">
              <template slot-scope="brat">
                  <malgo-brat-show 
                    v-bind:brat-data='brat.row.bratData' 
                    :brat-config-data='bratConfig' 
                    :brat-id='brat.row.id'>
                  </malgo-brat-show>
                </template>
            </el-table-column>
          </el-table>
        </div>   
      </el-tab-pane>
    </el-tabs>
  </el-dialog>
  <el-dialog title="原子词合并类型选择" :visible.sync='atomicTermCombineVisible' size="tiny">
          <el-form :model="combineAtomicTerm" label-position="left" label-width="110px">
          <el-form-item label="新原子词">
            <span style="font-size:15px">{{showName}}</span>
          </el-form-item>
          <el-form-item label="类型集合" >
            <el-select v-model="combineAtomicTerm.type" 
              filterable allow-create
              placeholder="请选择" style=" width:100%">
              <el-option
                v-for="item in combineAtomicTerm.types"
                :key="item.typeCode"
                :label="item.typeName"
                :value="item.typeCode">
              <span style="float: left">{{item.typeCode}}</span>
              <span style="float:right;color:#8492a6; font-size: 13px">{{item.typeName}}</span>
              </el-option>
            </el-select>
          </el-form-item>       
        </el-form>
        <el-row style="text-align:center">
          <el-col :span="24">
            <el-button type="primary" @click="combineAtomicTermConfirm">确认</el-button>
            <el-button @click='atomicTermCombineVisible=false'>关闭</el-button>
          </el-col>
        </el-row>
      </el-dialog>
  </div>
</template>
<script>
    import {selectAnnotationByTermTypeArr,getTypes} from '@/api/type'
    import {config,addCombineAtomicTerm} from '@/api/annotation'
    import MalgoBratShow from '@/components/MalgoBratShow.vue'
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false,
          a:'hello Janine',
          loading:false,
          annotationArr:[],
          size:'tiny',
          bratConfig:null,
          showName:'',
          multipleSelection:[],
          atomicTermCombineVisible:false,
          combineAtomicTerm:{
            type:null,
            types:[]
          }
        };
      },
      created() {
          var self=this
          
      } ,
      methods: {
        queryType(){
          var self=this;
          getTypes()
          .then((response)=>{
            var serverTypes=response.data
            var i=0,leni=serverTypes.length
              for(;i<leni;i++){
                if(!serverTypes[i].typeCode.endsWith('-unconfirmed')){
                  self.combineAtomicTerm.types.push({
                    typeName: serverTypes[i].typeName,
                    typeCode: serverTypes[i].typeCode
                  })
                }
              }
          })
        },
        combineAtomicTermConfirm(){
          var self=this
          addCombineAtomicTerm(self.multipleSelection,self.annotation.combineAnno,self.showName,self.combineAtomicTerm.type)
          .then((response)=>{
            if(response.succ==true){
              self.initTable()
              self.atomicTermCombineVisible=false
            }else{
              self.$message.error(response.msg);
            }
          })
        },
        HandleSelectionChange(val){
          var self=this
          Object.keys(val).forEach((key)=>{
            self.multipleSelection.push(val[key].id)
          })
          // self.multipleSelection=val
        },
        batchUpdate(){
          var self=this
          if(self.multipleSelection.length>0){
              console.log(self.multipleSelection)
              self.atomicTermCombineVisible=true
              self.queryType()
          }else{
            self.$message({
              type:'warning',
              message:'请先勾选标注记录！'
            });
          }
        },
        close(){
          var self=this
          self.annotationArr=[]
        },
        refresh(){
          var self=this
          if(self.bratConfig==null){
              // self.loading=true
              config()
              .then(function(response){
                self.bratConfig=response
                // self.loading=false
                self.initTable()
              }) 
            }else{
               self.initTable()
            }
            var showName=''
            Object.keys(self.annotation.combineAnno).forEach((key)=>{
              showName+=self.annotation.combineAnno[key].term
            })
            self.showName=showName
        },
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        },
        initTable(){
          var self=this
          self.loading=true
          self.annotationArr=[]
          console.log(self.annotation.combineAnno)
          selectAnnotationByTermTypeArr(self.annotation.combineAnno)
            .then((response)=>{
              if(response.succ==true){
                self.annotationArr=response.data
              }else{
                self.$message.error(response.msg)
              }
              self.loading=false
            })
        },
        tabClick(tab,event){
          var self=this
          self.showName=''
          let tabIndex = tab.$options.propsData.name
          if(tabIndex=='combineAnno'){
            self.size='small'
            self.refresh()
            var showName=''
            Object.keys(self.annotation.combineAnno).forEach((key)=>{
              showName+=self.annotation.combineAnno[key].term
            })
            self.showName=showName
          }else{
            self.size='tiny'
          }
        }
      },
      components:{
        'malgo-brat-show':MalgoBratShow
      }
    }
</script>


