<template>
    <div v-bind:id="'brat-result-'+bratId"></div>
</template>
<script>
  export default {
    props: ['bratData', 'bratConfigData', 'bratId'],
    data () {
      return {
        dispatcher:null,
        webFontURLs: [
          'static/brat/fonts/Astloch-Bold.ttf',
          'static/brat/fonts/PT_Sans-Caption-Web-Regular.ttf',
          'static/brat/fonts/Liberation_Sans-Regular.ttf'
        ],
        tspanLengths:[]        
      }
    },
    created () {

    },
    mounted () {
      return this.marking();
    },
    methods: {
      refresh(){
      },
      //在指定位置画出标注数据,并且绑定鼠标事件
      marking () {   
        if(!$('#brat-result-'+this.bratId).hasClass('hasSVG')){
          this.dispatcher=Util.embed('brat-result-' + this.bratId, this.bratConfigData, this.bratData,this.webFontURLs)
          }else{
          } 
          if(this.dispatcher!=null){
            this.dispatcher.on('doneRendering', this.bindMouseOperation);
          }
        }
      }
    }
</script>


